import axios from 'axios'
// import {
//   Message
// } from 'vant'
import router from '@/router';


import cookies from '@/util/cookies'



//  axios 
const service = axios.create({
  baseURL: process.env.VUE_APP_API,
  headers: {
    'Content-Type': 'application/json',
    'Access-Control-Allow-Origin': '*',
    "Access-Control-Allow-Headers": "Content-Type"
  },
  timeout: 5000 // 
})

// let lastUpdateToken = new Date();
// 
service.interceptors.request.use(async config => {
  // 
  // token-- ['X-Token']key 
  // config.headers['X-Token'] = util.cookies.get('token')
  // Bearer 
  let authorizationPrefix = 'Bearer '
  config.headers['Authorization'] = cookies.get('token');
  return config
}, error => {
  // 
  Promise.reject(error)
})

// 
service.interceptors.response.use(
  response => {
    // dataAxios  axios  data
    const dataAxios = response.data
    // 
    console.log(dataAxios)
    const {
      code
    } = dataAxios
    //  code 
    if (code === undefined) {
      //  code   D2Admin 
      return Promise.reject(dataAxios)
    } else {
      //  code  
      switch (code) {

        case 200:
          // [  ] code === 0 
          return dataAxios.data

        default:
          //  code
          console.log('code')
          return Promise.reject(dataAxios)
          break
      }
    }
  },
  error => {
    console.log(error)
    if (error && error.response) {
      switch (error.response.status) {
        case 422:
          error.message = '';

        case 400:
          error.message = '';
          break
        case 401:
          error.message = '';
          return router.push({
            name: 'login'
          })
          // break
        case 403:
          error.message = '';
          break
        case 404:
          error.message = `: ${error.response.config.url}`;
          break
        case 408:
          error.message = '';
          break
        case 500:
          error.message = '';
          break
        case 501:
          error.message = '';
          break
        case 502:
          error.message = '';
          break
        case 503:
          error.message = '';
          break
        case 504:
          error.message = '';
          break
        case 505:
          error.message = 'HTTP';
          break
        default:
          break
      }
    }
    // errorLog(error)
    return Promise.reject(error)
  }
)




let getHeader = function () {
  let authorizationPrefix = 'Bearer'
  return authorizationPrefix + cookies.get('token');
}
export default service